/*图文列表样式*/
@import "assets/fonts/iconfont.css";
@import "assets/css/reset.css";
.header-height {
  padding-top: .4rem;
  padding-bottom: .5rem;
}
.mint-header.is-fixed {
  z-index: 3;
}
.mint-header {
  background-color: #fff !important;
  color: #000 !important;
  border-bottom: 0.01rem solid rgba(223, 223, 223, 0.43);
}
.mint-popup-bottom {
  width: 100%;
}
.mint-swipe .mint-swipe-items-wrap .mint-swipe-item > img {
  width: 100%;
  height: 100%;
}
.userpopup-tp {
  height: .4rem;
  line-height: .4rem;
  border-bottom: 1px solid #f1f1f1;
  padding: 0 .1rem;
  background: rgba(223, 223, 223, 0.43);
}
.mint-indicator-wrapper {
  z-index: 9999;
}
.mint-indicator-mask {
  z-index: 999;
}
.mint-checkbox-input:checked + .mint-checkbox-core {
  background-color: #ef4f4f;
  border-color: #ef4f4f;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
  height: 100%;
  position: relative;
  background-color: #fff !important;
  overflow: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.detail-box .dec img {
  width: 100%;
}
.info p,
.info div,
.info img {
  width: 100%;
}
.goodsdetail img {
  width: 98%;
  margin-left: 1%;
}



